body {
  font-family: sans-serif;
  background-color: #f4f4f4;
  color: #333;
  margin: 0;
  padding: 2rem;
  transition: background 0.3s, color 0.3s;
}

h1 {
  text-align: center;
  margin-bottom: 2rem;
}

.app {
  max-width: 600px;
  margin: 0 auto;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  background: white;
  margin-bottom: 1rem;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
}

.character {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.character img {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  object-fit: cover;
}

.character {
  background: rgba(0, 0, 0, 0.05);
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
}

body.dark {
  background-color: #1e1e1e;
  color: #f4f4f4;
}

body.dark li {
  background-color: #2a2a2a;
}

body.dark .character {
  background: rgba(255, 255, 255, 0.05);
}

body.dark input,
body.dark button {
  background-color: #333;
  color: #f4f4f4;
  border: 1px solid #666;
}

body.dark input::placeholder {
  color: #bbb;
}
